import { TooltipPlacement } from 'antd/es/tooltip';
import { CSSProperties } from 'react';
import {
  BaseGuideOption,
  BaseInternalGuideOption,
  IGuideDataItem,
  IGuideExternal,
  IGuidePreview,
  IObserverSelectors,
} from '../types';

/** 用户自定义配置 */
export type PopoverGuideData = IGuideDataItem<PopoverGuideOption>[];

export interface PopoverGuideOption extends BaseGuideOption {
  /** 引导类型: PopoverGuide */
  type: 'PopoverGuide';
  /** 配置选择器 */
  selectors: IObserverSelectors<string>;
  /** 弹框内容 */
  content: IPopoverContent;
  /** 自定义弹框样式 */
  style?: IPopoverStyle;
  /** 引导触发方式。取值: none(默认，直接展示弹框), hover(悬停呼吸灯后展示引导弹框) */
  trigger?: PopoverTrigger;
}

/** 内部使用的配置 */
export type InternalPopoverGuideOption = PopoverGuideOption & BaseInternalGuideOption<IPopoverListener>;

export interface IPopoverContent {
  /** 标题 */
  title?: string;
  /** 内容描述 */
  desc?: string;
  /** 默认：知道了 */
  okText?: string;
  /** 预览图 */
  preview?: IGuidePreview;
  /** 链接文本 */
  external?: IGuideExternal;
}

export interface IPopoverStyle {
  /** 颜色主题。默认 default 为紫色主题，white 为黑白主题 **/
  theme?: 'default' | 'white';
  /** 层级 */
  zIndex?: number;
  /** 对话框对齐方式 */
  align?: TooltipAlignConfig;
  /** 对话框相对目标元素的位置 */
  placement?: TooltipPlacement;
  /** 用于调整呼吸灯的位置, 设置的CSS样式将直接作用到 .guide-popover-attach-node 元素上 */
  dotStyle?: CSSProperties;
}

export interface IPopoverListener {
  onShow?: () => void;
  onHide?: () => void;
  onClickOk?: () => void;
  onClickLink?: (linkInfo: { url: string; text: string }) => void;
}

export interface TooltipAlignConfig {
  points?: [string, string];
  offset?: [number | string, number | string];
  targetOffset?: [number | string, number | string];
  overflow?: { adjustX: boolean; adjustY: boolean };
  useCssRight?: boolean;
  useCssBottom?: boolean;
  useCssTransform?: boolean;
}

export enum PopoverTrigger {
  None = 'none',
  Hover = 'hover',
}
