import { BaseGuideOption, IGuideDataItem } from '../components';

/**
 * 本地缓存key值
 */
export const GuideCacheKey = 'guide-cache-key-xxxxx-00000';
export const GuideDevCacheKey = 'guide-dev-cache-key-xxxxx-00000';

/**
 * 本地缓存的数据结构
 */
export interface ICacheInfo {
  // 配置化引导的缓存数据
  guides: IGuideCache[];
  // devtool缓存
  devtool: Record<string, IGuideDevtoolCache>;
  // 其他自定义缓存
  extra: Record<string, any>;
}

export interface IGuideDevtoolCache {
  // 选中引导id
  checked?: string[];
  // 编辑框内容
  content?: string;
}
export interface IGuideCache {
  // 引导 id
  id: string;
  // 过期时间
  outdateTime: number | string;
}

export class CacheService {
  static cacheInfo: ICacheInfo = { guides: [], devtool: {}, extra: {} };

  static cacheKey = GuideCacheKey;

  static get defaultOutdateTime() {
    return Date.now() + 14 * 24 * 60 * 60000; // 若不提供过期时间，则默认上线后14天
  }

  static getOutdateTime(presetTime: number | string | undefined, cacheTime?: number | string) {
    const _cacheTime = Number(cacheTime);
    if (presetTime) {
      presetTime = new Date(presetTime).getTime();
      return _cacheTime && _cacheTime < presetTime ? _cacheTime : presetTime;
    } else {
      return _cacheTime || this.defaultOutdateTime;
    }
  }

  static isOutdated(outdateTime?: number | string) {
    return outdateTime ? new Date(outdateTime).getTime() <= Date.now() : false;
  }

  static init<T extends IGuideDataItem<BaseGuideOption>>(data: T[], devMode?: boolean): Map<string, IGuideCache> {
    this.cacheKey = devMode ? GuideDevCacheKey : GuideCacheKey;
    this.cacheInfo = this._get<ICacheInfo>(this.cacheKey) || this.cacheInfo;

    // eslint-disable-next-line @typescript-eslint/no-use-before-define
    const baseCacheMap = getBaseCacheMap(data);
    const retCacheMap = new Map<string, IGuideCache>();
    const localCacheMap = new Map<string, IGuideCache>();

    this.cacheInfo.guides?.forEach(it => {
      localCacheMap.set(it.id, it);
    });
    this.cacheInfo.guides = [];

    baseCacheMap?.forEach((cache, id) => {
      const localItem = localCacheMap.get(id);
      const currItem = {
        ...cache,
        outdateTime: this.getOutdateTime(cache.outdateTime, localItem?.outdateTime as number),
      };

      retCacheMap.set(id, currItem);
      this.cacheInfo.guides.push(currItem);
    });

    this._set(this.cacheInfo);

    return retCacheMap;
  }

  static remove(ids: string[] = []) {
    this.cacheInfo.guides = this.cacheInfo.guides.filter(it => !ids.includes(it.id));
    this._set(this.cacheInfo);
  }

  static set<T = any>(baseKey: 'guides' | 'devtool' | 'extra', cacheKey: string, cacheData: T) {
    switch (baseKey) {
      case 'guides':
        const newCacheData = this.cacheInfo.guides.concat(cacheData as unknown as IGuideCache[]);
        this.cacheInfo.guides = this.dedupeGuideCache(newCacheData);
        break;
      case 'devtool':
        this.cacheInfo.devtool[cacheKey] = cacheData;
        break;
      case 'extra':
      default:
        this.cacheInfo.extra[cacheKey] = cacheData;
        break;
    }
    this._set(this.cacheInfo);
  }

  static get<T = any>(baseKey: 'guides' | 'devtool' | 'extra', key: string): T {
    switch (baseKey) {
      case 'guides':
        // @ts-ignore
        return this.cacheInfo.guides;
      case 'devtool':
        // @ts-ignore
        return this.cacheInfo.devtool[key];
      case 'extra':
      default:
        return this.cacheInfo.extra[key];
    }
  }

  private static _set<T = any>(data: T) {
    localStorage.setItem(this.cacheKey, JSON.stringify(data));
  }

  private static _get<T>(key: string): T {
    const cache = localStorage.getItem(key);
    return cache ? JSONParse(cache) : null;
  }

  private static dedupeGuideCache(data: IGuideCache[]): IGuideCache[] {
    const map = new Map<string, IGuideCache>([]);

    data?.forEach(it => {
      map.set(it.id, it);
    });

    return Array.from(map.values());
  }
}

export const JSONParse = (content: string) => {
  try {
    return content ? JSON.parse(content) : null;
  } catch (error) {
    return null;
  }
};

export const getBaseCacheMap = <T extends BaseGuideOption, U extends IGuideDataItem<T>>(
  data: U[]
): Map<string, IGuideCache> => {
  const cacheMap: Map<string, IGuideCache> = new Map<string, IGuideCache>();

  for (const group of data) {
    for (const ins of group.instances) {
      cacheMap.set(ins.id, {
        id: ins.id,
        outdateTime: (ins.outdateTime as string) || (CacheService.defaultOutdateTime as number),
      });
    }
  }

  return cacheMap;
};
