import { IUserInfo } from '@utils/user';
import { cloneDeep } from 'lodash';
import { BaseGuideOption, guideComponentMap, GuideComponentType, IGuide, IGuideDataItem } from '../components';
import {
  CacheService,
  createDOMObserver,
  IObserverGuide,
  IDOMObserver,
  IGuideCache,
  isUserAccessible,
  querySelectors,
  UNKNOWN_PATHNAME,
  GuideLogger,
  getPathIdentifier,
} from '../utils';

export class GuideRender {
  /**
   * 开发模式
   */
  static devMode: boolean;
  /**
   * 用户信息
   */
  static userInfo: IUserInfo;
  /**
   * 上报埋点
   */
  static logger: GuideLogger;
  /**
   * 本地缓存的引导信息
   */
  static cachedMap = new Map<string, IGuideCache>();
  /**
   * 记录运行时注册的引导实例，按 pathname 分组
   */
  static guideMap = new Map<string, IGuide[]>([[UNKNOWN_PATHNAME, []]]);
  /**
   * 监听DOM变化
   */
  static observer: IDOMObserver<IObserverGuide>;

  /**
   * 校验引导配置
   */
  static validate<T extends BaseGuideOption>(option: T, userInfo: IUserInfo) {
    // 校验用户角色
    if (!isUserAccessible(option.userType || [], userInfo)) return;

    return true;
  }

  /**
   * 注册引导实例
   */
  static register<T extends BaseGuideOption>(data: IGuideDataItem<T>[]) {
    cloneDeep(data).forEach(item => {
      const { pathname, instances } = item;
      const guideList = this.guideMap.get(pathname || UNKNOWN_PATHNAME) || [];
      instances.forEach(ins => {
        const valid = this.validate(ins, this.userInfo);
        if (valid) {
          const compo = guideComponentMap.get(ins.type as GuideComponentType);
          if (compo) {
            const [GuideComponent, builtinPriority] = compo;
            const priority = [builtinPriority, Array.isArray(ins.priority) ? ins.priority[0] || 0 : ins.priority || 0];
            const logParams = Object.assign({}, { guide_trace_id: ins.id, guide_type: ins.type }, ins.logParams);
            const option = { ...ins, pathname, priority, logParams };

            guideList.push(new GuideComponent(option));
          }
        }
      });

      this.guideMap.set(pathname || UNKNOWN_PATHNAME, guideList);
    });
  }

  /**
   * 初始化
   */
  static init<T extends BaseGuideOption>(data: IGuideDataItem<T>[], userInfo: IUserInfo, devMode?: boolean) {
    const node: HTMLElement = document.body;

    this.devMode = Boolean(devMode);
    this.userInfo = userInfo;
    this.observer = createDOMObserver(node, this.scheduleTask.bind(this));
    this.logger = new GuideLogger(userInfo);
    this.cachedMap = CacheService.init(data, devMode);

    this.register(data);
  }

  /**
   * 重新打开引导
   */
  static reopen(ids: string[]) {
    this.guideMap.forEach(guides => {
      guides.forEach(guide => {
        if (ids.includes(guide.option.id)) {
          guide.option.isOutdated = false;
          guide.option.isMounted = false;
        }
      });
    });

    GuideRender.flush('', getPathIdentifier());
    GuideRender.observer.trigger();
    CacheService.remove(ids);
  }

  /**
   * 刷新引导任务队列
   */
  static flush(prevPathname: string, currPathname: string) {
    const oldInstances = this.guideMap.get(prevPathname);
    const newInstances = this.guideMap.get(currPathname);
    const unknownInstances = this.guideMap.get(UNKNOWN_PATHNAME);

    this.observer.clear();

    oldInstances?.forEach(guide => guide.clear());
    newInstances?.forEach(guide => guide.start());
    unknownInstances?.forEach(guide => guide.start());
  }

  /**
   * 调度渲染任务
   */
  static scheduleTask(node: HTMLElement, guideMap: Map<string, IObserverGuide>) {
    if (!guideMap.size) return;

    const taskQueue: IObserverGuide[] = Array.from(guideMap.values());
    taskQueue.sort((a, b) => parseInt(b.priority.join('')) - parseInt(a.priority.join('')));

    const refTask = taskQueue[0];
    const filteredTaskQueue = refTask
      ? taskQueue.filter(
          it =>
            it.runWithNoPriority || (it.priority[0] === refTask.priority[0] && it.priority[1] === refTask.priority[1])
        )
      : [];

    filteredTaskQueue.forEach(item => {
      const { selectors, render, clear } = item;
      const active = querySelectors(selectors, node);
      active ? render() : clear();
    });
  }

  /**
   * 销毁
   */
  static destroy() {
    this.observer?.destroy();
  }

  /**
   * 更新本地缓存
   */
  static updateCache() {
    const cachedData: IGuideCache[] = [];

    this.guideMap.forEach(guides => {
      guides.forEach(guide => {
        const oldCache = this.cachedMap.get(guide.option.id);
        const newCache = { id: guide.option.id, outdateTime: guide.option.outdateTime as number };

        // fix: 若存在相同id的多个guide，则取最早过期时间
        if (oldCache) {
          newCache.outdateTime = CacheService.getOutdateTime(newCache.outdateTime, oldCache.outdateTime);
        }

        cachedData.push(newCache);
        this.cachedMap.set(guide.option.id, newCache);
      });
    });

    CacheService.set<IGuideCache[]>('guides', '', cachedData);
  }
}
