import { BaseGuideOption, BaseInternalGuideOption, IGuideExternal, IObserverSelectors } from './types';
import { CacheService } from '../utils';
import { GuideRender } from '../core';

export abstract class BaseGuide<
  T extends BaseGuideOption,
  U extends BaseGuideOption & BaseInternalGuideOption<P>,
  P extends Record<string, any>
> {
  constructor(private rawOption: T) {}

  abstract option: U;
  abstract observerSelectors: IObserverSelectors;
  abstract render(): void;

  protected extendsOption(listeners: P): U {
    const outdateTime = CacheService.getOutdateTime(
      this.rawOption.outdateTime,
      GuideRender.cachedMap.get(this.rawOption.id)?.outdateTime as number
    );
    const isOutdated = CacheService.isOutdated(outdateTime);

    const commonOption: BaseInternalGuideOption<P> = {
      isOutdated,
      isMounted: false,
      destroyHook: new DestroyHook(),
      listeners,
    };

    return {
      ...this.rawOption,
      ...commonOption,
      outdateTime,
    } as unknown as U;
  }

  protected setOutdate() {
    this.option.isOutdated = true;
    this.option.outdateTime = Date.now();
    GuideRender.updateCache();
  }

  protected clear() {
    if (!this.option.isMounted) return;

    this.option.isMounted = false;
    this.option.destroyHook?.call();
  }

  protected start() {
    if (this.option.isOutdated) return;
    GuideRender.observer.observe(this.option.id, {
      id: this.option.id,
      priority: this.option.priority as number[],
      runWithNoPriority: this.option.runWithNoPriority,
      selectors: this.observerSelectors,
      render: () => this.render(),
      clear: () => this.clear(),
    });
  }

  protected guardRender(mount: () => void) {
    if (this.option.isMounted || this.option.isOutdated) return;
    this.option.isMounted = true;

    mount();
  }
}

export class DestroyHook {
  private destroyCallback!: () => void;

  use(callback: () => void) {
    this.destroyCallback = callback;
  }

  call() {
    this.destroyCallback?.();
  }
}

export const getExternalInfo = (external?: IGuideExternal): undefined | { text: string; url: string } => {
  if (!external) return;

  const isObj = typeof external === 'object';
  const defaultText = '帮助中心';
  const defaultUrl = 'https://school.jinritemai.com/doudian/web/';
  return isObj
    ? { text: external.text || defaultText, url: external.url || defaultUrl }
    : { text: defaultText, url: defaultUrl };
};
