import React, { useEffect, useRef } from 'react';
import { InternalHintGuideOption, HintHidden, HintPlacement } from './option';

export const HintComponent = (props: InternalHintGuideOption) => {
  const ref = useRef<HTMLSpanElement>(null);
  const { text = 'New', destroyHook, listeners, style, placement, hidden = HintHidden.Clicked } = props;
  const { onClick, onShow, onHide } = listeners;

  const destroy = () => {
    ref.current?.remove();
  };

  const handleClick = () => {
    onHide?.();
    onClick?.();
    destroy();
  };

  useEffect(() => {
    onShow?.();
    destroyHook?.use(destroy);

    if (hidden === HintHidden.Clicked) {
      const parentElement = ref.current?.parentElement as HTMLElement;
      parentElement?.addEventListener('click', handleClick, { once: true });
      return () => {
        parentElement?.removeEventListener('click', handleClick);
      };
    }
  }, []);

  return (
    <span
      className={`guide-hint guide-hint-placement-${placement || HintPlacement.Right}`}
      style={style}
      onClick={hidden === HintHidden.Clicked ? onClick : () => {}}
      ref={ref}
    >
      {text || 'New'}
    </span>
  );
};
