import { BaseGuideOption, IGuideDataItem } from '../components';

export const filterInstances = async <T extends BaseGuideOption, U extends IGuideDataItem<T>>(
  data: U[],
  filter: (ins: T) => Promise<boolean>
): Promise<U[]> => {
  const newData: U[] = [];

  for (const group of data) {
    const newItem = { ...group, instances: [] };

    for (const ins of group.instances) {
      const valid = await filter(ins);

      if (valid) {
        newItem.instances.push(ins);
      }
    }

    newData.push(newItem);
  }

  return newData;
};
