import { GuideRender } from './core';
import { CacheService, filterInstances, isPageLoaded, watchRouteChange } from './utils';
import GuideData from './data';
import { BaseGuideOption, GuideDevtool, IGuideDataItem } from './components';
import { IUserInfo } from '@utils/user';
import { getABTestConfig } from '../abTest';
export class BuyinGuideSDK {
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  static unwatch = () => { };

  /** 初始化配置化引导 */
  static init(userInfo: IUserInfo, devtool?: boolean) {
    isPageLoaded({ delay: 1000 })
      .then(() => GuideData.reduce((acc, curr) => acc.concat(curr), [] as IGuideDataItem<BaseGuideOption>[]))
      .then(data => this.filterABTest(data))
      .then(data => {
        GuideRender.init(data, userInfo, devtool);

        this.unwatch = watchRouteChange((prev, curr) => GuideRender.flush(prev, curr));

        // 引导配置面板
        if (devtool) {
          GuideDevtool.init(data);
        }
      })
      .catch(err => {
        console.error('引导初始化失败: ', err);
      });
  }

  /** 重新打开引导 */
  static reopen(ids: string[]) {
    GuideRender.reopen(ids);
  }

  /** 检查 AB 试验配置 */
  static async filterABTest<T extends IGuideDataItem<BaseGuideOption>>(data: T[]): Promise<T[]> {
    return await filterInstances(data, async ins => {
      if (ins.abTest) {
        try {
          const { key, defaultValue = false, activeValue = true } = ins.abTest;
          const result = await getABTestConfig(key, defaultValue);
          return result === activeValue;
        } catch {
          return false;
        }
      } else {
        return true;
      }
    });
  }

  /** 缓存统一管理工具 - 设置缓存 */
  static setCache<T = any>(key: string, data: T) {
    CacheService.set<T>('extra', key, data);
  }

  /** 缓存统一管理工具 - 读取缓存 */
  static getCache<T = any>(key: string): T {
    return CacheService.get<T>('extra', key);
  }

  /** 销毁配置化引导 */
  static destroy() {
    this.unwatch();
    GuideRender.destroy();
  }
}
