import { IObserverSelectors } from '../components';

/**
 * 根据单个选择器查询元素
 */
export const querySelector = (selector: string, node?: HTMLElement) => {
  if (node) {
    return node.querySelector(selector);
  } else {
    return document.querySelector(selector);
  }
};

/**
 * 根据多个选择器查询元素
 */
export const querySelectors = (selectors: IObserverSelectors, node?: HTMLElement) => {
  const { targetSelector, relatedSelectors } = selectors;
  const targetSelectors = Array.isArray(targetSelector) ? targetSelector : [targetSelector];
  const existed = (relatedSelectors || []).concat(targetSelectors).every(it => querySelector(it, node));
  return existed;
};
