import React from 'react';
import { PopoverComponent } from './component';
import { InternalPopoverGuideOption, IPopoverListener, PopoverGuideOption, PopoverTrigger } from './option';
import { addComponent } from '../../utils';
import { BaseGuide } from '../base';
import { IGuideLogParams, IObserverSelectors } from '../types';
import { GuideRender } from '../../core';
import './index.scss';

export class PopoverGuide extends BaseGuide<PopoverGuideOption, InternalPopoverGuideOption, IPopoverListener> {
  option!: InternalPopoverGuideOption;
  observerSelectors!: IObserverSelectors;

  constructor(rawOption: PopoverGuideOption) {
    super(rawOption);

    this.option = this.extendsOption({
      onShow: () => {
        GuideRender.logger.tapShow(this.option.logParams as IGuideLogParams);
      },
      onClickLink: linkInfo => {
        GuideRender.logger.tapLinkClick({
          ...(this.option.logParams as IGuideLogParams),
          button_for: linkInfo.text,
        });
      },
      onClickOk: () => {
        this.setOutdate();
        GuideRender.observer.disconnect(this.option.id, true);
        GuideRender.logger.tapButtonClick(this.option.logParams as IGuideLogParams);
      },
    });

    this.option.runWithNoPriority = Boolean(this.option.trigger === PopoverTrigger.Hover);
    this.observerSelectors = this.option.selectors;
  }

  render() {
    this.guardRender(() => {
      addComponent(this.option.selectors.targetSelector, <PopoverComponent {...this.option} />, {
        targetStyle: {
          position: 'relative',
        },
        overrideTargetStyle: false,
      });
    });
  }
}
