import React from 'react';
import { DevtoolComponent } from './component';
import { addComponent, getPathIdentifier } from '../../utils';
import { GuideRender } from '../../core';
import { BaseGuideOption, IGuideDataItem } from '../types';
import './index.scss';

export class GuideDevtool {
  /** 初始化面板 */
  static init(data: IGuideDataItem<BaseGuideOption>[]) {
    addComponent(document.body, <DevtoolComponent data={data} onPreview={data => this.preview(data)} />);
  }

  /** 预览 */
  static preview(data: IGuideDataItem<BaseGuideOption>[]) {
    GuideRender.guideMap.forEach(guides => {
      guides.forEach(guide => {
        guide.clear();
      });
    });

    GuideRender.guideMap.clear();
    GuideRender.cachedMap.clear();
    GuideRender.register(data);
    GuideRender.flush('', getPathIdentifier());
    GuideRender.observer.trigger();
  }
}
