export enum UserType {
  INTERNAL_BUSINESS = 11,
  ECOM_PLATFORM = 21,
  BRAND_MERCHANTS = 22,
  INSTITUTIONAL_SERVICE_PROVIDERS = 23,
  DAREN = 24,
}

/** 引导公共字段 */
export interface BaseGuideOption {
  /** 引导唯一ID */
  id: string;
  /** 引导组件类型 */
  type: string;
  /** 默认所有角色都生效。用户角色类型取值为：
   * 11 - 内部服务商(小店商家);
   * 21 - 电商平台;
   * 22 - 品牌商家(第三方商家);
   * 23 - 机构;
   * 24 - 达人;
   */
  userType?: UserType[];
  /** 引导过期时间。默认上线后 14 天过期 */
  outdateTime?: number | string;
  /** 引导默认按优先级进行顺序渲染 */
  priority?: number | number[];
  /** 引导不按优先级进行顺序渲染，而是命中了就直接渲染 */
  runWithNoPriority?: boolean;
  /** 埋点参数 **/
  logParams?: IGuideLogParams;
  /** AB 试验配置 */
  abTest?: IABTestConfig<string | number | boolean>;
  isOutdated?: boolean;
  isMounted?: boolean;
}

/** 引导内部公共字段 */
export interface BaseInternalGuideOption<T> {
  isOutdated: boolean;
  isMounted: boolean;
  destroyHook: IDestroyHook;
  listeners: T;
}

export interface IObserverOption {
  id: string;
  priority: number[];
  selectors: IObserverSelectors;
  render: () => void;
  clear: () => void;
}

export interface IObserverSelectors<T = string | string[]> {
  /** 目标元素的选择器 */
  targetSelector: T;
  /** 跟目标元素同时期存在的其他元素 */
  relatedSelectors?: string[];
  /** 引导弹框所在的父容器。默认 document.body */
  popupContainerSelector?: string;
}

export interface IGuideDataItem<T> {
  /** 引导生效时的 location.pathname */
  pathname: string;
  /** 引导实例列表 */
  instances: T[];
}

export type IGuideExternal =
  | boolean
  | {
      /** 链接文本。默认: 帮助中心 */
      text?: string;
      /** 跳转地址。默认: 跳转到抖音电商学习中心*/
      url?: string;
    };

export interface IGuidePreview {
  /**
   * 预览图统一放在 global/assets/img/guides/configurable 文件夹中
   * 将图片使用 import 方式导入到 data/images.ts 文件中
   * imgUrl: 导出的图片变量名称
   */
  imgUrl: string;
  /** 图片宽度 */
  width?: string;
  /** 图片高度 */
  height?: string;
}

/** 引导埋点上报参数 */
export interface IGuideLogParams {
  /** 引导跟踪唯一标识. 默认使用 guide_id 作为 guide_trace_id **/
  guide_trace_id?: string;
  /** 引导类型: 'TourGuide' | 'PopoverGuide' | 'HintGuide' **/
  guide_type?: 'TourGuide' | 'PopoverGuide' | 'HintGuide';
  page_name?: string;
  user_id?: string;
  author_id?: string;
  button_for?: string;
}
export interface IABTestConfig<T> {
  /** 配置参数键名 */
  key: string;
  /** 获取配置参数值失败时的默认值，默认 false */
  defaultValue?: T;
  /** 引导生效时的配置参数值，默认 true 时生效 */
  activeValue?: T;
}

export interface IGuide {
  option: BaseGuideOption;
  start: () => void;
  clear: () => void;
}

export interface IDestroyHook {
  use: (cb: () => void) => void;
  call: () => void;
}
