import React from 'react';
import { BaseGuide } from '../base';
import { InternalTourGuideOption, ITourGuideListener, TourGuideOption } from './option';
import { TourComponent } from './component';
import { addComponent } from '../../utils';
import { IGuideLogParams, IObserverSelectors } from '../types';
import { GuideRender } from '../../core';
import './index.scss';

export class TourGuide extends BaseGuide<TourGuideOption, InternalTourGuideOption, ITourGuideListener> {
  option!: InternalTourGuideOption;
  observerSelectors!: IObserverSelectors;

  constructor(rawOption: TourGuideOption) {
    super(rawOption);

    this.option = this.extendsOption({
      onShow: () => {
        GuideRender.logger.tapShow(this.option.logParams as IGuideLogParams);
      },
      onHide: () => {
        this.setOutdate();
        GuideRender.observer.disconnect(this.option.id, true);
        GuideRender.logger.tapButtonClick(this.option.logParams as IGuideLogParams);
      },
    });

    this.observerSelectors = {
      targetSelector: this.option.steps.map(it => it.targetSelector),
    };
  }

  render() {
    this.guardRender(() => {
      addComponent(document.body, <TourComponent {...this.option} />);
    });
  }
}
