import React, { FC, useEffect, useRef, useState } from 'react';
import { getPathIdentifier, JSONParse, CacheService, IGuideDevtoolCache } from '../../utils';
import { BaseGuideOption, IGuideDataItem } from '../types';

interface IDevtoolProps {
  data: IGuideDataItem<BaseGuideOption>[];
  onPreview: (data: IGuideDataItem<BaseGuideOption>[]) => void;
}

export const DevtoolComponent: FC<IDevtoolProps> = ({ data, onPreview }) => {
  const [expand, setExpand] = useState(false);
  const [checked, setChecked] = useState<string[]>([]);
  const [content, setContent] = useState<string>('');
  const [panelWidth, setPanelWidth] = useState<number>(500);
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const panelRef = useRef<HTMLDivElement>(null);
  const dragctrlRef = useRef<HTMLDivElement>(null);

  const handlePreview = () => {
    const content = textareaRef.current?.value || '';
    const data: IGuideDataItem<BaseGuideOption>[] = JSONParse(content) || [];
    if (!data.length) return;

    onPreview(data);

    CacheService.set<IGuideDevtoolCache>('devtool', getPathIdentifier(), {
      checked,
      content,
    });
  };

  const handleCheck = (id: string) => {
    let newChecked = [];
    if (checked.includes(id)) {
      newChecked = checked.filter(it => it !== id);
    } else {
      newChecked = [...checked, id];
    }

    const newCheckedData = getCheckedData(newChecked);
    const newContent = JSON.stringify(newCheckedData, null, 2);
    handleUpdate(newChecked, newCheckedData.length ? newContent : '');
  };

  const getCheckedData = (ids: string[]) => {
    const checkdData: IGuideDataItem<BaseGuideOption>[] = [];
    if (ids.length) {
      data.forEach(it => {
        const item: IGuideDataItem<BaseGuideOption> = { ...it, instances: [] };
        it.instances.forEach(ins => {
          if (ids.includes(ins.id)) {
            item.instances.push(ins);
          }
        });
        if (item.instances.length) {
          checkdData.push(item);
        }
      });
    }
    return checkdData;
  };

  const handleUpdate = (checked?: string[], content?: string) => {
    setChecked(checked || []);
    setContent(content || '');
  };

  useEffect(() => {
    if (expand) {
      let startOffset = 0;

      const onMouseMove = (e: MouseEvent) => {
        setPanelWidth(startOffset - e.pageX);
      };
      const onMouseUp = () => {
        document.removeEventListener('mousemove', onMouseMove);
        document.removeEventListener('mouseup', onMouseUp);
      };
      const onMouseDown = (e: MouseEvent) => {
        if (panelRef.current) {
          startOffset = panelRef.current.offsetWidth + e.pageX;
          document.addEventListener('mousemove', onMouseMove);
          document.addEventListener('mouseup', onMouseUp);
        }
      };

      dragctrlRef.current?.addEventListener('mousedown', onMouseDown);
      return () => {
        dragctrlRef.current?.removeEventListener('mousedown', onMouseDown);
        document.removeEventListener('mousemove', onMouseMove);
        document.removeEventListener('mouseup', onMouseUp);
      };
    }
  }, [expand]);

  useEffect(() => {
    if (expand) {
      const cachedInfo = CacheService.get<IGuideDevtoolCache>('devtool', getPathIdentifier()) || {
        checked: [],
        content: '',
      };
      // NOTE: 重新打开时，不恢复 checked 状态，因为配置可能已经更新
      handleUpdate([], cachedInfo?.content);
    }
  }, [expand]);

  if (expand) {
    const pathname = getPathIdentifier();
    const currPathData: BaseGuideOption[] = data
      .filter(it => !it.pathname || it.pathname === pathname)
      .reduce((acc, curr) => acc.concat(curr.instances), [] as BaseGuideOption[]);

    return (
      <div ref={panelRef} className="guide-devtool show-panel" style={{ width: panelWidth }}>
        <div className="guide-devtool-header">
          <span>新手引导配置面板</span>
          <span>
            <button className="guide-devtool-button" onClick={handlePreview}>
              预览
            </button>
            <button className="guide-devtool-button" onClick={() => setExpand(false)}>
              关闭
            </button>
          </span>
        </div>
        <div className="guide-devtool-body">
          <div className="guide-devtool-label">当前页面生效的引导列表</div>
          {currPathData.length ? (
            <table className="guide-devtool-table">
              <thead>
                <tr>
                  <th>操作</th>
                  <th>引导ID</th>
                  <th>引导组件</th>
                  <th>优先级</th>
                  <th>用户</th>
                  <th>过期时间</th>
                </tr>
              </thead>
              <tbody>
                {currPathData.map(it => {
                  return (
                    <tr
                      key={it.id}
                      className={checked.includes(it.id) ? 'checked' : ''}
                      onClick={() => handleCheck(it.id)}
                    >
                      <td>
                        <input type="checkbox" readOnly checked={checked.includes(it.id)}></input>
                      </td>
                      <td>{it.id}</td>
                      <td>{it.type}</td>
                      <td>{it.priority || '-'}</td>
                      <td>{(it.userType || []).join(',') || '-'}</td>
                      <td>{it.outdateTime || '-'}</td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          ) : (
            <div className="guide-devtool-empty">本页暂无新手引导</div>
          )}
          <div className="guide-devtool-label">引导配置项</div>
          <textarea
            ref={textareaRef}
            className="guide-devtool-editor"
            rows={15}
            placeholder={getPlaceholderContent()}
            value={content}
            onChange={evt => handleUpdate(checked, evt.target.value)}
          ></textarea>
          <div className="guide-devtool-label">
            配置示例参考:
            <a
              href="https://bytedance.feishu.cn/docx/doxcnn9Zh82TBqz8htU7Phbqv0v#"
              target="_blank"
              rel="noopener noreferrer"
            >
              https://bytedance.feishu.cn/docx/doxcnn9Zh82TBqz8htU7Phbqv0v#
            </a>
          </div>
        </div>
        <div ref={dragctrlRef} className="guide-devtool-dragctrl" />
      </div>
    );
  } else {
    return (
      <div className="guide-devtool show-icon" onClick={() => setExpand(true)}>
        新手引导
      </div>
    );
  }
};

export const getPlaceholderContent = () => {
  return `引导配置格式:
    [
      { 
        "pathname": "",
        "instances": [
          {
            "id": "xxxx",
            "type": "PopoverGuide",
            "outdateTime": "2021-01-01",
            ...
          }
        ]
      }
    ]`;
};
