import React from 'react';
import { HintComponent } from './component';
import { InternalHintGuideOption, HintGuideOption, IHintListener } from './option';
import { BaseGuide } from '../base';
import { addComponent } from '../../utils';
import { IGuideLogParams, IObserverSelectors } from '../types';
import { GuideRender } from '../../core';
import './index.scss';

export class HintGuide extends BaseGuide<HintGuideOption, InternalHintGuideOption, IHintListener> {
  option!: InternalHintGuideOption;
  observerSelectors!: IObserverSelectors;

  constructor(rawOption: HintGuideOption) {
    super(rawOption);

    this.option = this.extendsOption({
      onShow: () => {
        GuideRender.logger.tapShow(this.option.logParams as IGuideLogParams);
      },
      onHide: () => {
        this.setOutdate();
        GuideRender.logger.tapButtonClick(this.option.logParams as IGuideLogParams);
      },
    });

    this.observerSelectors = this.option.selectors;
  }

  render() {
    this.guardRender(() => {
      addComponent(this.option.selectors.targetSelector, <HintComponent {...this.option} />, {
        targetStyle: {
          position: 'relative',
        },
        overrideTargetStyle: false,
      });

      GuideRender.observer.disconnect(this.option.id);
    });
  }
}
