import { IUserInfo } from '@utils/user';
import { IGuideLogParams } from '../components';
import { Log } from '@global/domain/logger/base';

export class GuideLogger extends Log {
  userInfo: IUserInfo;

  constructor(userInfo: IUserInfo) {
    super();
    this.userInfo = userInfo;
  }

  private get commonParams() {
    const {
      user_id,
      origin_uid,
      author_level,
      is_real_one_shop,
      is_possess_login,
      shop_id,
      shop_type,
      buyin_account_id,
      shop_login_identity,
    } = this.userInfo;
    return {
      EVENT_ORIGIN_FEATURE: 'TEMAI',
      url: location.href,
      url_path: location.pathname,
      page_name: location.pathname,
      user_id,
      author_level,
      shop_type,
      user_level: author_level, // 作者等级
      author_id: shop_type === 24 ? origin_uid : '', // 达人 id
      shop_id: shop_type === 11 ? shop_id : '', // 小店商家 id
      inst_id: shop_type === 23 ? buyin_account_id : '', // 机构 id
      is_bind: is_real_one_shop ? 1 : 0, // 是否人店一体
      bind_status: is_real_one_shop ? 1 : 0, // 是否人店一体
      is_fake: Number(is_possess_login) === 1, // 是否运营附身,
      shop_login_identity, // 商家账号主子账号
    };
  }

  tapShow(params: IGuideLogParams) {
    this.send('guide_show_event', {
      ...this.commonParams,
      ...params,
    });
  }

  tapLinkClick(params: IGuideLogParams) {
    this.send('guide_link_click_event', {
      ...this.commonParams,
      ...params,
    });
  }

  tapButtonClick(params: IGuideLogParams) {
    this.send('guide_button_click_event', {
      ...this.commonParams,
      ...params,
    });
  }
}
