import ReactDOM from 'react-dom';
import { AlignClassType, AlignType, IOffsetType } from '../components/tours/option';
import { querySelector } from './selector';

/**
 * 将组件 Mount 到指定元素中
 */
export const addComponent = (
  elem: string | Element | null,
  component: JSX.Element,
  options?: {
    targetStyle?: Partial<CSSStyleDeclaration>;
    overrideTargetStyle?: boolean;
  }
) => {
  const selector = typeof elem === 'string' ? elem : '';
  const target = (selector ? querySelector(selector) : elem) as HTMLElement;

  if (!target) {
    console.error(`[addComponent] target not found: ${selector}`);
    return;
  }

  const container = document.createDocumentFragment();
  // @ts-ignore
  ReactDOM.render(component, container);
  addStyle(target, options?.targetStyle, options?.overrideTargetStyle);
  target.appendChild(container);
};

/**
 * 给元素设置样式
 */
export const addStyle = (elem: HTMLElement, style?: Partial<CSSStyleDeclaration>, override = true) => {
  if (style) {
    Object.keys(style).forEach(key => {
      elem.style[key] = (override ? '' : elem.style[key]) || style[key];
    });
  }
};

/**
 * 获取元素偏移位置
 */
export const getOffset = (element: HTMLElement): IOffsetType => {
  const { body } = document;
  const docEl = document.documentElement;
  const scrollTop = window.pageYOffset || docEl.scrollTop || body.scrollTop;
  const scrollLeft = window.pageXOffset || docEl.scrollLeft || body.scrollLeft;
  const x = element.getBoundingClientRect();
  return {
    top: x.top + scrollTop,
    width: x.width,
    height: x.height,
    left: x.left + scrollLeft,
  };
};

/**
 * 获取对齐样式
 */
export const getPlacement = (
  placement: AlignType,
  {
    left,
    width,
    contentWidth,
    containerWidth,
    top,
    containerHeight,
  }: {
    left: number;
    width: number;
    contentWidth: number;
    containerWidth: number;
    top: number;
    height: number;
    contentHeight: number;
    containerHeight: number;
  }
): AlignClassType => {
  let alignClass: AlignClassType;

  if (['top', 'bottom'].includes(placement)) {
    // 当引导元素宽度小于目标元素时需设置对齐
    if (width < contentWidth) {
      // 目标元素在引导容器左侧时，则左对齐
      if (left < containerWidth / 2) {
        alignClass = 'align-left';
      } else {
        alignClass = 'align-right';
      }
    } else {
      alignClass = 'align-auto';
    }
  } else if (top < containerHeight / 2) {
    alignClass = 'align-top';
  } else {
    alignClass = 'align-bottom';
  }

  return alignClass;
};
