import { IObserverSelectors } from '../components';
import { debounce } from 'lodash';

export interface IDOMObserver<T> {
  observe: (id: string, params: T) => void;
  disconnect: (id: string, triggerOnce?: boolean) => void;
  clear: () => void;
  trigger: () => void;
  destroy: () => void;
}

export interface IObserverGuide {
  id: string;
  priority: number[];
  runWithNoPriority?: boolean;
  selectors: IObserverSelectors;
  render: () => void;
  clear: () => void;
}

export const createDOMObserver = <T = Record<string, any>>(
  node: HTMLElement,
  handler: (node: HTMLElement, obMap: Map<string, T>) => void
): IDOMObserver<T> => {
  let ob: MutationObserver;
  const observerMap = new Map<string, T>([]);

  const trigger = () => {
    handler(node, observerMap);
  };

  const observe = (id: string, params: T) => {
    observerMap.set(id, params);
  };

  const disconnect = (id: string, triggerOnce?: boolean) => {
    observerMap.delete(id);

    if (triggerOnce) {
      setTimeout(() => trigger());
    }
  };

  const clear = () => {
    observerMap.clear();
  };

  const destroy = () => {
    clear();
    ob?.disconnect();
  };

  ob = new MutationObserver(debounce(trigger, 100, { leading: false, trailing: true }));
  ob.observe(node, { subtree: true, childList: true });

  return { trigger, observe, disconnect, clear, destroy };
};
