import React, { CSSProperties, FC, useCallback, useEffect, useRef, useState } from 'react';
import { AlignClassType, InternalTourGuideOption, InternalTourGuideStep } from './option';
import classnames from 'classnames';
import { getOffset, getPlacement, querySelector } from '../../utils';
import { debounce } from 'lodash';
import { getExternalInfo } from '../base';
import guideImages from '../../data/images';

export const TourComponent: FC<InternalTourGuideOption> = props => {
  const {
    steps: rawSteps,
    backText = '上一步',
    nextText = '下一步',
    finishText = '完成',
    okText = '知道了',
    external,
    destroyHook,
    listeners,
    bodyOverflow = 'hidden',
    tourWrapperStyle = {},
    connectWrapperStyle,
    contentWrapperStyle = {},
  } = props;
  const { zIndex } = { zIndex: 999000, ...(tourWrapperStyle || {}) };
  const { width: contentWidth } = { width: 384, ...(contentWrapperStyle || {}) };
  const { onShow, onHide, onStepChange } = listeners;
  const externalInfo = getExternalInfo(external);

  const steps = (rawSteps as InternalTourGuideStep[]).map(it => {
    it.targetDOM = querySelector(it.targetSelector) as HTMLElement;
    return it;
  });

  const maskRef = useRef<HTMLDivElement>(null);
  const wrapperRef = useRef<HTMLDivElement>(null);
  const contentRef = useRef<HTMLDivElement>(null);
  const bodyOverflowRef = useRef(document.body.style.overflow);

  const [wrapperStyle, setWrapperStyle] = useState<CSSProperties>({ opacity: 0 });
  const [contentAlign, setAlign] = useState<AlignClassType>();

  const [stepIndex, setStepIndex] = useState(0);
  const activeStep: InternalTourGuideStep = steps[stepIndex];

  const destroy = () => {
    scrollHandler.unlock();
    maskRef.current?.remove();
    wrapperRef.current?.remove();
    window.removeEventListener('resize', resizeStyle);
    onHide?.(stepIndex, steps.length);
  };

  const scrollHandler = {
    lock() {
      document.body.style.overflow = bodyOverflow;
    },
    unlock() {
      document.body.style.overflow = bodyOverflowRef.current;
    },
  };

  const handleStep = (delta: number) => {
    let newStep = stepIndex + delta;

    if (newStep > steps.length - 1) {
      destroy();
    } else {
      newStep = Math.min(steps.length - 1, newStep);
      newStep = Math.max(0, newStep);
      setStepIndex(newStep);
    }

    onStepChange?.(newStep, stepIndex, steps.length);
  };

  const resizeStyle = useCallback(
    debounce(() => {
      if (!activeStep) return;
      const { targetDOM } = activeStep;
      if (targetDOM) {
        const { left, top, width, height } = getOffset(targetDOM);
        const offset = 16;
        setWrapperStyle({
          opacity: 1,
          left: left - offset,
          top: top - offset,
          width: width + offset * 2,
          height: height + offset * 2,
          padding: 4,
        });
        const alignClass = getPlacement(activeStep.alignType, {
          left,
          width,
          contentWidth: parseInt(`${contentWidth}` as string, 10),
          containerWidth: window.innerWidth,
          top,
          height,
          contentHeight: contentRef.current?.offsetHeight || 168,
          containerHeight: window.innerHeight,
        });
        setAlign(alignClass);

        scrollHandler.lock();
      }
    }, 50),
    [activeStep]
  );

  useEffect(() => {
    destroyHook?.use(destroy);
    resizeStyle();
    onShow?.(stepIndex, steps.length);

    window.addEventListener('resize', resizeStyle);
    return () => {
      window.removeEventListener('resize', resizeStyle);
    };
  }, [activeStep]);

  return (
    <>
      <div ref={maskRef} className="guide-tour-outer-mask" style={{ zIndex }} />
      <div
        ref={wrapperRef}
        className={classnames(`guide-tour-wrapper`, `guide-tour-${activeStep.alignType || 'bottom'}`)}
        style={{ ...wrapperStyle, ...tourWrapperStyle, zIndex: parseInt(zIndex as string, 10) + 1 }}
      >
        <div className="guide-tour-inner-mask" />
        <div className="guide-tour-active-wrapper" />

        <div className="guide-tour-connect-wrapper" style={connectWrapperStyle}>
          <div className="guide-tour-round-outer">
            <div className="guide-tour-round-inner" />
          </div>
          <div className="guide-tour-connect-bar" />
        </div>

        <div
          ref={contentRef}
          className={classnames(`guide-tour-content-wrapper`, `guide-tour-${contentAlign}`)}
          style={{ width: 384, ...contentWrapperStyle }}
        >
          <div className="guide-tour-content">
            <div className={`guide-tour-step guide-tour-step__${activeStep.previewAlign || 'horizontal'}`}>
              {activeStep.preview && (
                <div className="guide-tour-step__preview">
                  <img
                    src={
                      activeStep.preview.imgUrl.startsWith('http')
                        ? activeStep.preview.imgUrl
                        : guideImages[activeStep.preview.imgUrl]
                    }
                    width={activeStep.preview.width || '100%'}
                    height={activeStep.preview.height || 'auto'}
                    alt={'activeStep'}
                  />
                </div>
              )}
              <div className="guide-tour-step__content">
                <div className="guide-tour-step__title">{activeStep.title}</div>
                <div className="guide-tour-step__desc">{activeStep.content}</div>
              </div>
            </div>
          </div>

          <div className="guide-tour-actions">
            <div className="guide-tour-actions-extra">
              {Boolean(externalInfo) && <span onClick={() => window.open(externalInfo?.url)}>{externalInfo.text}</span>}
            </div>
            <div className="guide-tour-actions-buttons">
              {stepIndex > 0 && (
                <button className="ant-btn ant-btn-dashed" onClick={() => handleStep(-1)}>
                  {backText}
                </button>
              )}
              <button className="ant-btn ant-btn-primary" onClick={() => handleStep(1)}>
                {stepIndex === steps.length - 1 ? (stepIndex > 0 ? finishText : okText) : nextText}
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
