import { Badge, Button, Popover, PopoverProps } from 'antd';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { InternalPopoverGuideOption, PopoverTrigger } from './option';
import { getExternalInfo } from '../base';
import guideImages from '../../data/images';

export const PopoverComponent = (props: InternalPopoverGuideOption) => {
  const { style, listeners, selectors, content, destroyHook, trigger = PopoverTrigger.None } = props;
  const { popupContainerSelector } = selectors;
  const { onShow, onHide, onClickOk, onClickLink } = listeners;
  const { title, desc, okText = '知道了', preview, external } = content;
  const { zIndex = 0, align, placement = 'top', dotStyle = {}, theme = 'default' } = style || {};
  const externalInfo = getExternalInfo(external);
  const isHoverTrigger = trigger === PopoverTrigger.Hover;

  const [visible, setVisible] = useState(true);
  const innerRef = useRef<HTMLSpanElement>(null);

  const visibleProps = useMemo<Partial<PopoverProps>>(() => {
    if (isHoverTrigger && visible) {
      return { trigger: 'hover' };
    } else {
      return { visible };
    }
  }, [visible, isHoverTrigger]);

  const getPopupContainer = useCallback(() => {
    const container = popupContainerSelector ? document.querySelector(popupContainerSelector) : null;
    return container || document.body;
  }, []);

  const handleClickOK = () => {
    onClickOk?.();
    destroy();
  };

  const handleClickLink = () => {
    onClickLink?.(externalInfo as { url: string; text: string });
    window.open(externalInfo?.url);
  };

  const destroy = () => {
    setVisible(false);
    onHide?.();
    innerRef.current?.remove();
  };

  useEffect(() => {
    onShow?.();
    destroyHook?.use(destroy);
  }, []);

  return (
    <Popover
      {...visibleProps}
      overlayClassName={`guide-popover is-theme-${theme}`}
      destroyTooltipOnHide={{ keepParent: false }}
      getPopupContainer={() => getPopupContainer() as HTMLElement}
      placement={placement}
      // align={align}
      zIndex={zIndex}
      content={
        <div className="guide-popover-wrapper">
          {preview && (
            <div className="guide-popover-image">
              <img
                src={preview.imgUrl.startsWith('http') ? preview.imgUrl : guideImages[preview.imgUrl]}
                width={preview.width || '100%'}
                height={preview.height || 'auto'}
                alt={'preview'}
              />
            </div>
          )}
          <div className="guide-popover-title">{title}</div>
          <div className="guide-popover-desc" dangerouslySetInnerHTML={{ __html: desc as string }} />
          <div className="guide-popover-footer">
            <span className="guide-popover-external">
              {Boolean(externalInfo) && <span onClick={handleClickLink}>{externalInfo?.text}</span>}
            </span>
            <Button
              className="guide-popover-button"
              onClick={e => {
                e.preventDefault();
                handleClickOK();
              }}
            >
              {okText}
            </Button>
          </div>
        </div>
      }
    >
      {isHoverTrigger ? (
        <span
          ref={innerRef}
          className={`guide-popover-attach-node is-trigger-hover is-theme-${theme}`}
          style={dotStyle}
        >
          <Badge status="processing" />
        </span>
      ) : (
        <span ref={innerRef} className={`guide-popover-attach-node is-theme-${theme}`} />
      )}
    </Popover>
  );
};
