import { getQuery } from "@global/utils/routers/query";

export const getABTestConfig: (K: string, V?: any) => Promise<any> = (key, defaultValue = false) => {
  let val = defaultValue;

  // boe或mock场景,本地开发场景
  if (
    process.env.PROXY_ENV === 'mock' ||
    process.env.PROXY_ENV === 'boe' ||
    process.env.BUILD_ENV === 'boe' ||
    process.env.NODE_ENV === 'development'
  ) {
    // 通过url query获取配置参数信息
    const { _ab } = getQuery();
    try {
      val = JSON.parse(_ab)?.[key] ?? defaultValue;
    } catch (error) {
      val = defaultValue;
    }
  }

  return new Promise((resolve: any, reject: any) => {
    try {
      resolve({})
    } catch (e) {
      reject(e);
    }
  });
};