import { CSSProperties } from 'react';
import { BaseGuideOption, BaseInternalGuideOption, IGuideDataItem, IObserverSelectors } from '../types';

export type HintGuideData = IGuideDataItem<HintGuideOption>[];

export interface HintGuideOption extends BaseGuideOption {
  /** 引导类型: HintGuide */
  type: 'HintGuide';
  /** 配置选择器 */
  selectors: IObserverSelectors<string>;
  /** 提示文本消失时机：clicked-点击后消失; outdated-有效时间过期后消失。默认: clicked */
  hidden?: HintHidden;
  /** 提示文本。默认 New */
  text?: string;
  /** 提示文本位置 */
  placement?: HintPlacement;
  /** 提示文本样式 */
  style?: Partial<CSSProperties>;
}

export type InternalHintGuideOption = HintGuideOption & BaseInternalGuideOption<IHintListener>;

export enum HintPlacement {
  Right = 'right',
  RightTop = 'rightTop',
}

export enum HintHidden {
  Clicked = 'clicked',
  Outdated = 'outdated',
}

export interface IHintListener {
  onClick?: () => void;
  onShow?: () => void;
  onHide?: () => void;
}
