/**
 * 针对路由标识为空，则设置默认的 pathname
 */
export const UNKNOWN_PATHNAME = 'UNKNOWN_PATHNAME';

/**
 * 获取路由标识。默认按 pathname 分组
 */
export const getPathIdentifier = () => location.pathname;

/**
 * 监听路由变化
 */
export const watchRouteChange = (callback: (prev: string, curr: string) => void, fireImmediately = true) => {
  let prevPathname = '';
  let currPathname = getPathIdentifier();

  const handler = () => {
    prevPathname = currPathname;
    currPathname = getPathIdentifier();
    if (prevPathname !== currPathname) {
      callback(prevPathname, currPathname);
    }
  };

  window.addEventListener('popstate', handler);

  if (fireImmediately) {
    callback(prevPathname, currPathname);
  }

  return () => {
    window.removeEventListener('popstate', handler);
  };
};

/**
 * 页面是否 loaded
 */
export interface IsPageLoadedParam {
  delay?: number;
}
export const isPageLoaded = (option: IsPageLoadedParam): Promise<boolean> =>
  new Promise(resolve => {
    const checkReadyState = () => {
      if (document.readyState === 'complete') {
        setTimeout(() => resolve(true), option.delay || 0);
      }
    };
    document.onreadystatechange = checkReadyState;
    checkReadyState();
  });
